@import "../../../App.scss";

.links-container {
  background-color: rgba(134, 184, 250, 0.2);
  padding: 8px;
  margin-bottom: 80px;
  width: 90%;
  @media (max-width: 768px) {
    width: 100%;
  }
  @media (min-width: 768px) and (max-width: 991px) {
    margin-inline: auto;
  }
  h2 {
    background-color: $primary-color;
    color: white;
    font-size: 15px;
    padding: 8px 5px;
    font-weight: 600;
  }
  p {
    padding: 15px 5px;
  }
}
